body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/starwarsapi/static/media/bckgr.1aeaa166.jpg);
  /*background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0, 128, 128, 1) 100%);*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'STAR WARS FONT';
font-style: normal;
font-weight: normal;
src: local('STAR WARS FONT'), url(/starwarsapi/static/media/Starjedi.cc3019aa.ttf) format('truetype');
}

h1, h3 {
	font-family: 'STAR WARS FONT';
	font-weight: 200;
	color: #FFD700;
}
